import React, { FC, useState } from 'react';
import {
  StyledStepByStep,
  ImagesContent,
  TextContent,
  StepsList,
  Step,
  StepDescription,
} from './styled';
import { Text } from '../common/Text';
import CustomLink from '../common/Link';
import { StaticImage } from 'gatsby-plugin-image';
import { InternalLinks } from '../../utils/links';

type Step = {
  text: string;
  subText: string;
};

const steps: Step[] = [
  {
    text: 'You must be a Campaign Service Provider (CSP) with The Campaign Registry(TCR).',
    subText:
      'Not a CSP? We can help you get started.',
  },
  {
    text: 'Integrate with Telgorithm.',
    subText: '',
  },
  {
    text: 'Fund your account & start sending & receiving messages.',
    subText: '',
  },
];

const PaygIntegrationSteps: FC = () => {
  const [highlightedImage, setHighlightedImage] = useState(0);

  return (
    <StyledStepByStep>
      <TextContent>
        <Text.Title.Bold.lg>How it works:</Text.Title.Bold.lg>
        <StepsList>
          {steps.map((step, index) => (
            <Step
              key={step.text}
              onMouseEnter={() => setHighlightedImage(index + 1)}
              onMouseLeave={() => setHighlightedImage(0)}
            >
              <Text.Regular.Bold.lg>{index + 1 + '. ' + step.text}</Text.Regular.Bold.lg>
              <StepDescription>
                <Text.Regular.Normal.sm>{step.subText}</Text.Regular.Normal.sm>
              </StepDescription>
            </Step>
          ))}
        </StepsList>
        <CustomLink address={InternalLinks.GettingStarted} arrow>
          Learn more about integration
        </CustomLink>
      </TextContent>
      <ImagesContent highlightedImage={highlightedImage}>
        <StaticImage
          src="../../images/Common/CreateBrand.png"
          alt="Create brand"
          objectFit="contain"
        ></StaticImage>
        <StaticImage
          src="../../images/Common/Campaign.png"
          alt="Campaign"
          objectFit="contain"
        ></StaticImage>
        <StaticImage
          src="../../images/Common/Messages.png"
          alt="Messages"
          objectFit="contain"
        ></StaticImage>
      </ImagesContent>
    </StyledStepByStep>
  );
};

export default PaygIntegrationSteps;
